body {
  margin: 0;
  padding:0 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    'source-code-pro', 'Menlo', 'Monaco', 'Consolas', 'Courier New';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body {
  color: #d3d3d3;
  background-color: rgba(0, 0, 0, 0.5);
  /*  Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #4294ff, #01041f);
}
 
.main img {
  margin-right: 2rem;
}
h1 {
  border: solid 1px  #e47c28;
  background-color: #f07e21;
  text-align: center;
  color: #f0f5f1;
}
i,
a {
  color: #f1b7b7;
  font-size: 20px;
}
.logo {
  color: #fff;
}
i:hover,
a:hover {
  outline: none;
  color: #000;
}
p {
  font-size: 20px;
}

.main {
  font-size: 20px;
}